#draggableicon {
    display: flex;
    cursor: pointer;
    width:  50px;
    height: 50px;
    position: fixed;
    z-index: 2147483647150205020;
    background-color: #FFFFFF;
    text-align: center;
    top: 50%;
    right: 0;
    box-shadow: 0px 6px 5px rgba(0, 0, 0, 0.15);
    border: 1px solid #d3d3d3;
    border-radius: 10px 10px 10px 10px;
    background-image: url(https://i.imgur.com/rHtAb1N.png);
    background-size: contain;
    background-repeat: no-repeat;
}




#draggableicon:hover{
    background-color: #E1E1E1;
    box-shadow: 0px 6px 5px rgba(0, 0, 0, 0.15);
    border: 1px solid #d3d3d3;
    border-radius: 10px 10px 10px 10px;
    background-image: url(https://i.imgur.com/4rl2vI2.png);
    background-size:contain;
    background-repeat: no-repeat;

}

#extensionwrapper{
    bottom: 0;
    left: 0;
}
